import headers from "../../components/header_nav.vue"
import service from "../../service.js";
export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "兑换劵",
			Hlicon: "../../static/icon/left_whitch.png",
			subtitle:"我的劵",
			funlist:''
		}
	},
	methods:{
		receive(id){
			/* service.auth(this , service.api.service.get_exchange , 'POST' , {
				couponid:id,
				token:uni.getStorageSync('token')
			} , function(self,res){
				uni.showToast({
					icon:'none',
					title:res.info,
					duration:2000
				});
			}); */
			
			 uni.request({
				url:service.api.service.get_exchange,
				method:"POST",
				data:{
					couponid:id
					// token:uni.getStorageSync('token')
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if(res.code == 1){
						uni.showToast({
							icon: 'none',
							title: res.info
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: res.info,
							duration:2000
						});
					}
				}
			});
			
		},
		jump(desc){
			uni.navigateTo({
				url:'./user_rule?desc='+desc
			})
		}
	},
	onShow() {
		service.auth(this , service.api.service.exchange , 'POST' , {} , function(self,res){
			console.log(res);
			self.funlist = res.data.data
		})
	}
}
